body {
  background-color: darkkhaki;
}

.App {
  background-color: darkcyan;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;

  display: flex;
  flex-direction: column;
  align-items: center;
}

#to-do-form {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  /* background-color: darkolivegreen; */
  height: 100px;
}

#to-do-form input {
  /* margin: 20px; */

  background-color: goldenrod;
  border: 0;
  width: 230px;
  height: 50px;
  padding: 0 20px;
  font-size: 18px;
  border-radius: 5px;
  color: antiquewhite;
}

#to-do-form input::placeholder {
  color: gray;
}

#to-do-form input:focus, #to-do-input button:focus {
  outline: none;
  box-shadow: 0px 0px 10px cyan;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rebeccapurple;
  cursor: pointer;
  font-weight: bolder;
  color: antiquewhite;
  font-size: 18px;
}