.list {
    color: antiquewhite;
    width: 365px;
    background-color: darkseagreen;
    height: auto;
    border-radius: 5px;
    
    /* display: flex;
    justify-content: space-between; */
    /* align-items: ; */
    margin-bottom: 10px;
    /* overflow: hidden;
    white-space: nowrap;
    text-overflow: clip ellipsis; */
}

.list p {
    padding: 10px;
    position: relative;
}

.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
}

.faicons {
    font-size: 18px;
    margin: auto 0;
}

.list p input {
    background-color: transparent;
    border: none;
    color: antiquewhite;
    font-size: 15px;
    width: 320px;
}

.list p input:focus {
    outline: none;
}

.list:focus-within {
    background-color: goldenrod;
}

/* Text Hover */
.list p:before {
    content: attr(data-hover);
    z-index: 1;
    width: 150px;
    word-wrap: break-word;
    height: auto;
    visibility: hidden;
    opacity: 0;
    transition: opacity 1s ease-in-out;
    position: absolute;
    background-color: rgb(56, 53, 53);
    color: azure;
    padding: 10px;
    box-shadow: 1px 1px 1px azure ;
    bottom: 25px;
    left: 370px;
    border-radius: 5px;
}

.list p:hover:before {
    opacity: 1;
    visibility: visible;
}